:root {
  --trunk-color: #583928;
  --branch-color: #155132;
  --leaf-color: #2d6a3e;
  --flower-color: #93d17a;
  --logo-text-color: #35231b;
  --background-color: #fff9e2;
  --main-text-color: #593c1f;
  --alt-text-color: #556b2f;
  --head-text-color: #22551c;
  --title-text-color: var(--leaf-color);
}

html {
  -webkit-text-size-adjust: 100%;
  resize: both;
  background-image: url("watermark.aeec90e7.png");
  background-size: 320px;
}

body {
  color: var(--main-text-color);
  flex-direction: column;
  max-width: 1024px;
  margin: auto;
  padding: 0 10px;
  font-family: Arial, sans-serif;
  display: flex;
}

@media only screen and (width <= 600px) {
  body {
    padding: 0;
  }
}

@media only screen and (width <= 1024px) {
  body {
    margin: 0 10px;
  }
}

h1 {
  color: var(--title-text-color);
  font-size: 2em;
}

h2 {
  color: var(--title-text-color);
  font-size: 1.5em;
}

h3 {
  color: var(--title-text-color);
  font-size: 1em;
}

a {
  color: var(--title-text-color);
}

a:visited {
  color: var(--alt-text-color);
}

p {
  margin-bottom: 10px;
  font-size: 1em;
}

ul {
  margin-left: 20px;
  padding: 0;
  list-style-image: url("bullet.b535f962.png");
}

li {
  margin-bottom: 5px;
}

address {
  font-style: normal;
}

mark {
  background-color: var(--flower-color);
  color: var(--logo-text-color);
  outline: var(--branch-color) 4px solid;
  border: 1px solid #fff;
  border-radius: 15px;
  margin: 0 5px 5px;
  padding: 0 7px;
  display: inline-block;
}

header {
  background-color: var(--background-color);
  color: var(--alt-text-color);
  text-align: center;
  background-color: var(--background-color);
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  display: flex;
}

header aside {
  display: inline-block;
}

header img {
  width: 128px;
  margin: 0 20px 0 0;
}

header h1 {
  color: var(--logo-text-color);
  margin: 0;
}

header h2 {
  color: var(--title-text-color);
  margin: 0;
}

nav {
  border-bottom: var(--branch-color) 2px solid;
  background-color: var(--background-color);
}

nav a {
  border-width: 2px;
  border-color: var(--branch-color);
  color: var(--logo-text-color);
  background-color: color-mix(in srgb, var(--flower-color), var(--background-color));
  border-style: solid;
  border-radius: 5px 5px 0 0;
  margin: 0 2px;
  padding: 1px 6px;
  line-height: normal;
  text-decoration: none;
  display: inline-block;
  position: relative;
  top: 2px;
}

nav a:visited {
  color: var(--logo-text-color);
}

nav a.disabled {
  background-color: var(--background-color);
  border-bottom-width: 0;
  padding-bottom: 3px;
}

nav a:hover {
  background-color: var(--background-color);
}

@media only screen and (width <= 490px) {
  nav a {
    margin: 0;
    padding: 1px 3px;
    font-family: Arial Narrow, sans-serif;
  }
}

main {
  background-color: var(--background-color);
  flex: 1;
  height: 100%;
  margin: 0;
}

main p {
  line-height: 1.6;
}

news {
  background-color: var(--background-color);
  flex-wrap: wrap;
  flex: 1;
  justify-content: space-around;
  height: 100%;
  margin: 0;
  display: flex;
}

news p {
  line-height: 1.6;
}

news article {
  border: var(--flower-color) 2px solid;
  border-radius: 15px;
  width: 400px;
  margin: 2px;
  padding: 2px 5px;
}

news article p, news article h1, news article h2, news article h3 {
  margin: 0 0 6px;
}

footer {
  background-color: color-mix(in srgb, var(--background-color), transparent 50%);
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 50px;
  display: flex;
}

footer div {
  width: 250px;
}

footer h1 {
  font-size: 1em;
}
